.Home {
    /* margin: auto; */
    /* text-align: center; */
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    /* background: url("https://via.placeholder.com/1920x1280.png?text=Background+Image") top center; */
    background: url("../../assets/images/bg-home.jpg") top center;
    background-size: cover;
    margin-top: -75px;
}

  .Home:before {
    content: "";
    background: rgba(5, 13, 24, 0.3);
    position: absolute;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
  }
  
.Home .hero-container {
    position: relative;
    z-index: 2;
    min-width: 300px;
  }
  
  .Home h1 {
    margin: 0 0 0 10px 0;
    font-size: 64px;
    font-weight: 700;
    line-height: 56px;
    color: #fff;
    text-shadow: 2px 2px 10px #000000;
  }
  
  .Home p {
    color: #fff;
    margin-bottom: 50px;
    font-size: 26px;
    font-family: "Poppins", sans-serif;
    text-shadow: 2px 2px 10px #000000;
  }
  
  .Home p span {
    color: #fff;
    padding-bottom: 4px;
    letter-spacing: 1px;
    border-bottom: 3px solid #149ddd;
  }
  
  @media (min-width: 1024px) {
    .Home {
      background-attachment: fixed;
    }
  }
  
  @media (max-width: 768px) {
    .Home h1 {
      font-size: 28px;
      line-height: 36px;
    }
    .Home h2 {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 30px;
    }
    .Home .hero-container {
      margin-left: 5px;
  }
  }